//* ------------------------------------------------------------
//* ABSTRACTS: COLORS PARTIAL
//*
//* Place all global color variables and CSS custom properties here.
//* Only color definitions (SCSS variables or :root custom properties) belong in this file.
//* Do NOT put any CSS rules, selectors, or non-color variables here.
