:root {
  // Color Palette

  // Primary color and variations - deep purple family
  --primary-bg: #500f44; // Deep purple for strong contrast
  --primary-bg-mid: #751c7d; // Medium purple for balanced contrast
  --primary-bg-light: #c642cf; // Light purple for highlights
  --primary-bg-pale: #c493d2; // Muted pale purple for better text readability

  // Secondary color and variations - deep green family
  --secondary-bg: #0a2d08; // Deep forest green for strong contrast
  --secondary-bg-mid: #1a3317; // Dark olive green for text
  --secondary-bg-light: #42c439; // Bright green for accents
  --secondary-bg-pale: #a8d4a5; // Muted pale green for better text readability

  // Backgrounds
  --light-bg: #e1dede; // Light neutral background
  --mid-bg: #666; // Medium gray background
  --dark-bg: #000000; // Pure black for dark mode backgrounds
  --basic-vertical-navbar-title-bg-gradient: linear-gradient(to bottom, #fff, #eb3b5a, #eb3b5a, #eb3b5a);

  // Borders
  --primary-border: #500f44; // Deep purple border
  --primary-border-mid: #751c7d; // Deep purple border
  --light-border: #e1dede; // Light neutral border
  --primary-bright-error-border: #c642cf; // Bright for error messages on dark backgrounds
  --alt-border: #eb3b5a; // Bright for error messages on dark backgrounds

  // Text colors
  --dark-text: #1a1a1a; // Very dark gray for primary text
  --light-text: #e1dede; // Light gray for text on dark backgrounds
  --primary-text: #500f44; // Deep purple for accent text
  --primary-text-mid: #751c7d; // Deep purple for accent text
  --primary-text-bright: #c642cf; // Bright for error messages on dark backgrounds
  --secondary-text: #0a2d08; // Deep green for secondary text
  --dark-text-mid: #c0c0c0; // Medium gray for muted text
  --dark-text-link: #1a1a1a; // Very dark gray for maximum link contrast

  // FONTAWESOME STYLES
  --fa-primary-color: #500f44;
  --fa-secondary-color: #b89cbf;

  // Topnav
  --light-overlay: hsl(0deg 0% 42% / 50%); // Semi-transparent gray overlay
  --menu-overlay: hsl(0deg 0% 0% / 70%); // Dark overlay for mobile menu (70% opacity)
  --primary-gradient-light: #c642cf; // Light purple maintaining original lightness
  --secondary-gradient-light: #42c439; // Bright green maintaining original lightness

  // About
  --primary-underline-light: #c642cf; // Light purple underline maintaining original lightness
  --secondary-underline-light: #42c439; // Bright green underline maintaining original lightness

  // Contact
  --primary-shadow: #500f441a; // Purple shadow with transparency
  --dark-error: #751c7d; // mid purple for error messages on light backgrounds

  // Hero
  --light-text-hero: #e1dede; // White text for hero section

  // Calculator Page
  --calc-display-bg: #e1dede; //! NEED TO UPDATE THIS BASED ON ORIGINAL CSS

  // Alien Page Form Colors
  // ---------------------------------
  // Backgrounds
  --alien-primary-bg: #000; //! NEED TO UPDATE THIS BASED ON ORIGINAL CSS
  --alien-secondary-bg: #000; //! NEED TO UPDATE THIS BASED ON ORIGINAL CSS

  // Borders
  --alien-border-color: #000; //! NEED TO UPDATE THIS BASED ON ORIGINAL CSS
  --alien-accent-border-color: #000; //! (for border/text/bg) NEED TO UPDATE THIS BASED ON ORIGINAL CSS
  --alien-accent-secondary-border-color: #000; //! (for border/bg) NEED TO UPDATE THIS BASED ON ORIGINAL CSS

  // Text
  --alien-text-light: #d04949; //! NEED TO UPDATE THIS BASED ON ORIGINAL CSS
  --alien-text-dim: #d04949; //! NEED TO UPDATE THIS BASED ON ORIGINAL CSS
  --alien-accent: #000; //! (for text) NEED TO UPDATE THIS BASED ON ORIGINAL CSS

  // Effects
  --primary-glow: #c642cf; // Light purple glow effect

  // Skills Chart
  --skills-tab-inactive-bg: rgb(255 255 255 / 5%); // Subtle background for inactive tabs
  --skills-tab-inactive-text-opacity: 0.7; // Opacity for inactive tab text
  --skills-tab-hover-opacity: 0.9; // Opacity for tab hover state
}
