//* ------------------------------------------------------------
//* DROPDOWN COMPONENT PARTIAL
//*
//* Place all styles for the reusable .dropdown component here.
//* Includes BEM classes like .dropdown, .dropdown__button, .dropdown__content, etc.
//* Do NOT put global resets or page-level layout styles here.
//* Only styles specific to the dropdown component belong in this file.
//* ------------------------------------------------------------
@use '../A-abstracts/functions' as f;
@use '../A-abstracts/mixins';

.dropdown {
  width: 100%;
  padding-bottom: f.rem(20);

  li {
    width: 80%;
    margin: f.rem(24) auto;
  }

  &__title-group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: f.rem(16);
    cursor: pointer;
    color: var(--light-text);
    outline: none;
    background: linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
    padding: f.rem(16) f.rem(32);
    transition: all 0.3s ease;
    border: 1px solid var(--light-border);
    border-radius: f.rem(8);
    touch-action: manipulation;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
    @include mixins.breakpoint-down('485.98') {
      padding: f.rem(12) f.rem(8);
      font-size: f.rem(15);
      gap: f.rem(10);
    }

    // Better focus management
    &:hover {
      background: linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
      color: var(--light-text);
      border-color: var(--light-border);
    }

    &:focus {
      background: linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
      color: var(--light-text);
      border-color: var(--light-border);
    }

    &:focus-visible {
      outline: 2px solid var(--light-text);
      outline-offset: 2px;
    }

    // When dropdown is open: same gradient with reduced opacity
    &:has(~ .dropdown__content.show) {
      opacity: 0.7;

      &:not(:has(~ .dropdown__content.show)) {
        opacity: 1; // Explicitly reset opacity
        &:focus-visible {
          outline-color: var(--light-text);
        }
      }
    }
  }

  &__icon {
    font-size: f.rem(20);
    color: currentcolor;
    transition: transform 0.3s ease;
    display: inline-block;
    line-height: 1;
    flex-shrink: 0;

    // When dropdown is closed, hand points down (rotate from up to down)
    transform: rotate(180deg);
  }

  // When dropdown is open, rotate hand to point up (original position)
  &__title-group.dropdown-open &__icon {
    transform: rotate(0deg);
  }

  &__title {
    margin: 0;
    text-wrap: balance;
    line-height: 1.2;
  }

  &__subtitle {
    margin: 0;
    font-size: f.rem(13);
    color: rgb(255 255 255 / 80%);
    font-style: italic;
    font-weight: 400;
    line-height: 1.3;
    text-wrap: balance;

    // Ensure good contrast for accessibility
    @media (prefers-contrast: high) {
      color: #fff;
      font-weight: 500;
    }

    // Respect reduced motion preferences
    @media (prefers-reduced-motion: reduce) {
      transition: none;
    }
  }

  &__content {
    display: none; /* Initially hide the content */
    font-size: f.rem(14);
    padding: 10px;
    background: var(--light-bg);
    border-top: 1px solid var(--light-border);
    transition: all 0.3s ease;
    max-height: 70vh;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    @include mixins.breakpoint-down('485.98') {
      font-size: f.rem(13);
      padding: f.rem(8) f.rem(4);
    }

    li {
      border-bottom: 1px solid var(--light-border);
      padding: f.rem(12) f.rem(8);

      &:last-child {
        border-bottom: none;
      }

      .achievement {
        font-size: f.rem(17);
        font-weight: 600;
        color: var(--dark-text);
        margin-bottom: f.rem(4);
        line-height: 1.4;
        display: flex;
        align-items: flex-start;
        gap: f.rem(8);
      }

      .achievement-icon {
        color: var(--primary-bg);
        font-size: f.rem(16);
        margin-top: f.rem(2);
        flex-shrink: 0;
      }

      .source {
        font-size: f.rem(15); // Increased from 12 for better readability
        color: var(--secondary-bg-mid);
        font-style: italic;
        margin-left: f.rem(16);
        line-height: 1.4; // Improved from 1.3 for better readability

        // Style links within source to show they're clickable
        a {
          color: inherit;
          text-decoration: underline;
          text-decoration-color: var(--secondary-bg-mid);
          text-underline-offset: 2px;

          &:hover,
          &:focus {
            color: var(--dark-text);
            text-decoration-color: var(--dark-text);
          }
        }
      }
    }

    // Show content when 'show' class is added by JavaScript
    &.show {
      display: block;
    }
  }
}
.dropdown__content.show {
  display: block !important;
  opacity: 1 !important;
  visibility: visible !important;
  z-index: 9999 !important;
}
